body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home-wrapper {
  width: 100%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }
  .home-wrapper .container {
    margin: auto; }
    .home-wrapper .container .title {
      display: -webkit-inline-flex;
      display: inline-flex; }
      .home-wrapper .container .title h1 {
        background: url(/static/media/trianglified.12b62431.svg);
        background-size: 100% 100%;
        -webkit-text-fill-color: transparent;
        -webkit-background-clip: text;
        background-clip: text;
        font-size: 350%;
        -webkit-animation: h1-enter 0.3s ease-in;
                animation: h1-enter 0.3s ease-in;
        text-transform: uppercase;
        font-weight: 800;
        margin: 1%; }
    .home-wrapper .container h4 {
      color: #6180a5;
      -webkit-animation: activity-enter 0.3s ease-in;
              animation: activity-enter 0.3s ease-in;
      text-shadow: 0px 0px 0.2px rgba(255, 255, 255, 0.6); }
    .home-wrapper .container .nav {
      display: -webkit-inline-flex;
      display: inline-flex;
      margin: 2% 0; }
      .home-wrapper .container .nav .item {
        width: 100%;
        padding: 5%;
        background-color: #ff2d55;
        border-radius: 10px;
        color: white;
        font-weight: bold; }
        .home-wrapper .container .nav .item:hover {
          cursor: pointer; }
    .home-wrapper .container .social img {
      width: 5%; }

@-webkit-keyframes h1-enter {
  0% {
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
    opacity: 0; }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1; } }

@keyframes h1-enter {
  0% {
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
    opacity: 0; }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1; } }

@-webkit-keyframes activity-enter {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes activity-enter {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.playlist-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  min-height: 100vh; }
  .playlist-wrapper .playlist-container {
    width: 60%;
    margin: auto; }
    .playlist-wrapper .playlist-container iframe {
      width: 100%;
      border: 1px solid rgba(0, 0, 0, 0.1);
      border-radius: 10px; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

